@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap");

body {
  margin: 0;
  position: relative;
  font-family: "Poppins", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smooth: always;
  background-color: hsl(0, 0%, 95%);
  color: #272c5b;
}

* {
  font-family: "Poppins", "Helvetica Neue", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}

.content-page {
  position: relative;
  margin-top: 75px;
  min-height: calc(100vh - 75px);
  height: auto;
  overflow-x: hidden;
}

.footerContainer {
  position: relative;
  display: flex !important;
  flex-direction: column !important;
  justify-content: flex-end;
  z-index: 100;
}

@media only screen and (max-width: 1000px) {
  .content-page {
    margin-top: 60px;
    min-height: calc(100vh - 60px);
  }
}

.row {
  display: flex;
  align-items: center;
}

.row-space-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.space-evenly {
  display: flex;
  justify-content: space-evenly;
}

.align-flex-start {
  align-items: flex-start;
}
.align-center {
  align-items: center;
}
.align-flex-end {
  align-items: flex-end;
}

.no-select {
  user-select: none;
}

.display-none {
  display: none;
}

.red-outlined-button,
.disabled-outlined-button {
  padding: 10px 12.5px;
  background-color: transparent;
  color: #e15554;
  width: fit-content;
  border: 2px solid #e15554;
  font-weight: 500;
  font-size: 1em;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.disabled-outlined-button {
  color: #afafaf;
  border: 2px solid #afafaf;
}

.red-outlined-button:hover {
  background-color: #e15554;
  color: #fff;
  transition: all 0.2s ease-in-out;
}

.outlined-button {
  display: flex;
  align-items: center;
  padding: 10px 12.5px;
  background-color: transparent;
  color: #272c5b;
  width: fit-content;
  border: 2px solid #272c5b;
  font-weight: 500;
  font-size: 1em;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.outlined-button:hover {
  background-color: #272c5b;
  color: #fff;
  transition: all 0.2s ease-in-out;
}

.primary-button {
  display: flex;
  align-items: center;
  padding: 10px 12.5px;
  white-space: nowrap;
  font-size: 1em;
  font-weight: 500;
  border-radius: 10px;
  text-decoration: none;
  width: fit-content;
  border: solid 3px #272c5b;
  cursor: pointer;
  color: white !important;
  background-color: hsl(234, 40%, 25%);
  transition: all 0.1s ease-in-out;
}
.primary-button:hover {
  border: solid 3px hsl(234, 40%, 20%);
  background-color: hsl(234, 40%, 20%);
  transition: all 0.1s ease-in-out;
}

.underline {
  text-decoration: underline;
}

button {
  user-select: none;
}

.select-container {
  width: 300px;
  background-color: white;
  border-radius: 10px;
}

.primary-select {
  height: 50px;
  padding: 0 15px;
  width: 100%;
  font-size: 1em;
  color: #40489c;
  font-weight: 500;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
  outline: none;
  cursor: pointer;
  appearance: none;
  -moz-appearance: none; /* Firefox */
  -webkit-appearance: none; /* Safari and Chrome */
  background: url("./assets/arrow-down.svg");
  background-position: right;
  background-repeat: no-repeat;
  background-position-x: 265px;
}

.secondary-select {
  padding: 5px;
  width: 100%;
  font-size: 1em;
  color: #40489c;
  font-weight: 500;
  border: 0;
  border: 1.5px solid rgb(200, 200, 200);
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  appearance: none;
  -moz-appearance: none; /* Firefox */
  -webkit-appearance: none; /* Safari and Chrome */
  background: url("./assets/arrow-down.svg");
  background-position: right;
  background-repeat: no-repeat;
  background-position-x: 265px;
}

.label {
  font-size: 1em;
  font-weight: 600;
  margin-bottom: 10px;
}
.primary-input {
  position: relative;
  width: 100%;
  height: 45px;
  color: #40489c !important;
  padding: 0 15px;
  font-size: 1em;
  font-weight: 500;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
  outline: none;
  box-sizing: border-box;
}

.primary-input::placeholder {
  color: rgba(0, 0, 0, 0.35);
}

.primary-input-error {
  border-radius: 10px;
  box-shadow: 0 15px 25px hsla(0, 59%, 75%, 0.5) !important;
  border: 2.5px solid hsl(0, 59%, 75%) !important;
  margin-bottom: 10px;
}
.secondary-input-error {
  border: 3.5px solid hsl(0, 59%, 75%) !important;
}

.error-message {
  color: hsl(0, 59%, 50%);
  font-size: 0.9em;
}

.secondary-input {
  color: #40489c;
  padding: 0 10px;
  height: 45px;
  font-size: 1em;
  font-weight: 500;
  border: 0;
  border: 1.5px solid rgb(200, 200, 200);
  border-radius: 5px;
  width: 100%;
  outline: none;
  box-sizing: border-box;
  margin: 1px;
  transition: all 0.1s ease-in-out;
}

.secondary-input:is(:focus) {
  border: 2.5px solid hsl(235, 42%, 60%);
  transition: all 0.1s ease-in-out;
}

.secondary-input::placeholder {
  font-weight: 400;
  color: rgba(0, 0, 0, 0.3);
}

.half {
  width: 50%;
}
.input-half {
  width: 50%;
  min-width: 300px !important;
}

.half-section {
  width: 49% !important;
}

.one-third {
  width: 32% !important;
}

.two-thirds {
  width: 65% !important;
}

.submit-button:hover {
  background-color: hsl(234, 40%, 20%);
  border: solid 3px hsl(234, 40%, 20%);
  transition: all 0.1s ease-in-out;
}
