.content {
  width: clamp(300px, calc(100% - 25px), 950px);
}

.pageTitle {
  position: relative;
  margin: 0;
  height: 80px;
  width: fit-content;
  display: flex;
  margin-bottom: 65px;
  align-items: center;
}
.pageTitle:after {
  content: "";
  position: absolute;
  bottom: 15px;
  left: 0px;
  width: 100%;
  height: 5px;
  border-radius: 100px;
  background-color: #ff9633;
}
.listHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.1rem;
  font-weight: 500;
  background-color: white;
  width: 100%;
  min-height: 75px;
  box-sizing: border-box;
  padding: 15px 15px 15px 25px;
  border-radius: 10px;
  box-shadow: 0 5px 25px hsla(235, 59%, 75%, 0.15);
}

.headerText {
  margin-right: 15px;
}

.listing {
  position: relative;
  display: flex;

  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 105px;
  background-color: white;
  padding: 15px 15px 15px 0;
  box-sizing: border-box;
  margin: 15px 0;
  border-radius: 10px;
  box-shadow: 0 5px 25px hsla(235, 59%, 75%, 0.15);
  overflow: hidden;
}

.listingLeft {
  display: flex;
  align-items: center;
  width: fit-content;
  height: 100%;
  margin-left: 25px;
}

.listingColor {
  position: absolute;
  width: 7px;
  height: calc(100% + 30px);
  margin-right: 15px;
}

.listingImage {
  object-fit: cover;
  width: 75px;
  height: 75px;
  border-radius: 5px;
  margin-right: 5px;
}

.catColor {
  background-color: #6871d8;
}
.dogColor {
  background-color: #ffd232;
}
.rabbitColor {
  background-color: #e15554;
}
.birdColor {
  background-color: #03cea4;
}
.roddentColor {
  background-color: #78c3fb;
}

.listingContent {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.listingInfo {
  margin-left: 15px;
  width: 200px;
}

.listingTitle {
  font-weight: 600;
  margin: 0;
  text-decoration: none;
  color: #272c5b;
  font-size: 1.25rem;
  transform: translate(0);
  transition: all 0.1s ease-in-out;
}

.listingTitle:hover {
  color: #515ac8;
  transition: all 0.1s ease-in-out;
}

.listingAttributes {
  font-weight: 500;
  margin: 0;
  width: 100%;
  color: #515ac8;
}

.separator {
  width: 2px;
  height: 65px;
  background-color: hsl(235, 10%, 80%);
  margin-right: 15px;
  border-radius: 100px;
}

.price {
  font-weight: 500;
  color: #515ac8;
  font-size: 1.1rem;
}

.genderMale {
  color: #00a6ed;
}
.genderFemale {
  color: #d34f73;
}

.listingButtons {
  display: flex;
}

.listingBtn svg {
  padding: 0;
  width: 30px;
  height: 30px;
  color: #272c5b;
}
.listingBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  margin-left: 15px;
  border: 0;
  background-color: #eeeff6;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}

.listingBtn:hover {
  background-color: #e7e8ef;
  transition: all 0.1s ease-in-out;
}

@media screen and (max-width: 1050px) {
  .listing {
    height: auto;
  }
  .listingInfo {
    width: fit-content;
    margin-right: 25px;
  }
  .listingContent {
    flex-direction: column;
    align-items: flex-start;
  }
  .listingButtons {
    margin: 15px 0 0 25px;
  }

  .listingBtn {
    margin: 0;
    margin-right: 10px !important;
  }
}

@media screen and (max-width: 780px) {
  .content {
    width: calc(100% - 25px);
  }

  .listing {
    width: 100%;
  }
}

@media screen and (max-width: 575px) {
  .content {
    margin: 0 auto;
  }
  .pageTitle {
    margin-bottom: 25px;
  }

  .listHeader {
    flex-direction: column;
    font-size: 1.1em;
  }

  .headerText {
    text-align: center;

    margin-bottom: 15px;
  }

  .listingAttributes {
    display: flex;
    flex-direction: column;
  }

  .listingInfo {
    width: fit-content;
    margin-right: 15px;
  }
}
