.card {
  margin: 40px;
  min-width: 250px;
  width: 250px;
  height: fit-content;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
}

.shape {
  width: 250px;
  height: 100%;
}

.cardHead {
  position: relative;
  width: 100%;
  background-color: white;
  border-radius: 15px 15px 0 0;
}

.cat {
  color: #6871d8;
}
.dog {
  color: #ffd232;
}
.rabbit {
  color: #e15554;
}
.bird {
  color: #03cea4;
}
.rodent {
  color: #78c3fb;
}

.cardImg {
  position: absolute;
  top: 15px;
  left: 15px;
  box-sizing: border-box;
  width: 150px;
  height: 150px;
  object-fit: cover;
  z-index: 1;
  border-radius: 10px;
}

.cardBody {
  position: relative;
  padding: 0 15px 15px 15px;
  height: 175px;
  display: flex;
  background-color: white;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
  border-radius: 0 0 15px 15px;
}

.name {
  color: #2f356f;
  margin: 5px 0;
  font-weight: 600;
  font-size: 1.6rem;
  line-height: 1.75rem;
}

.identity {
  white-space: nowrap;
  margin: 0;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 1.5rem;
}
.genderFemale {
  color: #d34f73;
}
.genderMale {
  color: #00a6ed;
}

.age {
  color: #6871d8;
}

.city {
  font-weight: 500;
  margin: 5px 0;
  color: #40489c;
}
.caracteristicsTitle {
  color: #2f356f;
  margin: 0;
  font-size: 1.2rem;
  font-weight: 600;
}

.caracteristicsList {
  padding: 0;
  padding-left: 25px;
  margin: 0;
  font-size: 1rem;
  color: #6871d8;
  font-weight: 500;
  list-style-type: none;
}
.caracteristicsList > li:before {
  content: "-"; /* en dash */
  position: absolute;
  margin-left: -1.25em;
}

.like {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  box-sizing: border-box;
  border-radius: 15px;
  border: 2px solid rgba(0, 0, 0, 0.25);
  margin-right: 10px;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.likeIcon {
  width: 30px;
  fill: #e15554;
}

.likeIconInterior {
  fill: #fff;
  transition: all 0.15s ease-in-out;
}

.like:hover .likeIconInterior {
  fill: #e15554;
  stroke-width: 15px;
  stroke: #e15554;
  transition: all 0.15s ease-in-out;
}

.like:active {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15) inset;
}

.see {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 10px;
  box-sizing: border-box;
  background-color: #ff891a;
  color: white;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.see svg {
  width: 30px;
  height: 30px;
}

.see:hover {
  background-color: #ed7d31;
  transition: all 0.2s ease-in-out;
}

.see:active {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15) inset;
}

.see:hover svg {
  animation: card-arrow 0.5s forwards;
}

@keyframes card-arrow {
  0% {
    margin-left: 0px;
  }
  50% {
    margin-left: 10px;
  }
  100% {
    margin-left: 0px;
  }
}

@media screen and (max-width: 1000px) {
  .card {
    margin: 25px;
  }
}
