.selectBox {
  user-select: none;
  display: flex;
  width: 135px;
  font-weight: 500;
  box-sizing: border-box;
  flex-direction: column;
}

.optionsContainer {
  position: absolute;
  background-color: #404680;
  color: white;
  width: 135px;
  max-height: 0;
  border-radius: 10px 0 10px 10px;
  overflow: hidden;
  z-index: 100;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  transition: all 0.25s;
}

.defaultOption {
  background-color: #303670;
}

.selected {
  display: flex;
  padding: 0 12px;
  justify-content: space-between;
  align-items: center;
  background-color: #2f356f;
  border-radius: 10px 0 0 10px;
  color: white;
}

.selectBox .option,
.selected {
  display: flex;
  padding-left: 12px;
  height: 45px;
  align-items: center;
  font-size: 1rem;
  cursor: pointer;
  margin: 0;
}
.selectBox .option:hover {
  background-color: #505690;
}

.selectBox label {
  cursor: pointer;
}

.selectBox .option .radio {
  display: none;
}

.selectBox .optionsContainer::-webkit-scrollbar {
  width: 8px;
  background-color: #2f356f;
  border-radius: 0 8px 8px 0;
}

.selectBox .optionsContainer::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #7076b0;
}

.selectBox .optionsContainer.active {
  max-height: 160px;
  opacity: 1;
  overflow-y: scroll;
}

@media screen and (max-width: 750px) {
  .selectBox,
  .optionsContainer {
    width: 100px !important;
  }

  .selected,
  .option {
    font-size: 0.9em !important;
  }

  .selected {
    padding: 0 10px;
  }

}
