.pageTitle {
  position: relative;
  margin: 0;
  height: 80px;
  width: fit-content;
  display: flex;
  margin-bottom: 65px;
  align-items: center;
}
.pageTitle:after {
  content: "";
  position: absolute;
  bottom: 15px;
  left: 0px;
  width: 100%;
  height: 5px;
  border-radius: 100px;
  background-color: #ff9633;
}

.content {
  width: clamp(300px, 75%, 950px);
}

@media screen and (max-width: 575px) {
  .content {
    margin: 0 auto;
  }
}
