.nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  width: 310px;
  z-index: 1000;
  background-color: white;
  padding-bottom: 66px;
  height: calc(100vh - 75px);
  box-shadow: 10px 0 25px hsla(235, 25%, 50%, 0.1);
  transition: all 0.2s ease-in-out;
}

.wrapped {
  width: 75px;
  transition: all 0.2s ease-in-out;
}

.wrapped .navText {
  max-width: 0%;
  padding-left: 0;
  animation: padding 0.2s ease-in-out forwards;
}

@keyframes padding {
  0% {
    max-width: 100%;
  }

  100% {
    max-width: 0%;
  }
}

.wrapped .navLink {
  padding: 20px 25px;
  height: 66px;
  transition: all 0.1s ease-in-out;
}

.wrapped .navLink svg {
  padding: 0;
}

.wrapIcon {
  position: absolute;
}

.visible {
  opacity: 1;
  transition: all 0.3s ease-in-out;
}
.hidden {
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.wrapped .navLink:hover svg {
  padding: 0;
  transition: all 0.2s ease-in-out;
}

.wrapped .navLink:hover {
  transition: all 0.1s ease-in-out;
}

.navTop,
.navBot {
  display: flex;
  flex-direction: column;
}

.navLink {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 1.1em;
  width: 100%;
  box-sizing: border-box;
  white-space: nowrap;
  font-weight: 600;
  height: 66px;
  color: #2f356f;
  text-decoration: none;
  padding: 20px 25px;
  cursor: pointer;
}
.navLink:hover {
  background-color: #eeeff6 !important;
}

.navText {
  padding-left: 15px;
  overflow: hidden;
  max-width: 100%;
  user-select: none;
  transition: all 0.3s ease-in-out;
}

.navLink svg {
  width: 25px;
  height: 25px;
  transition: all 0.2s ease-in-out;
}

.navLink:hover svg {
  padding-right: 15px;
  transition: all 0.2s ease-in-out;
}

@media screen and (max-width: 1000px) {
  .nav {
    height: calc(100vh - 60px);
  }
}

@media screen and (max-width: 575px) {
  .nav {
    flex-direction: row;
    height: fit-content;
    width: 100%;
    position: fixed;
    bottom: 0;
    padding: 0;
  }

  .wrapBtn {
    display: none !important;
  }

  .navText {
    display: none;
  }

  .navTop,
  .navBot {
    flex-direction: row;
  }
  .navLink:hover svg {
    padding-right: 0;
  }
}
@media screen and (max-width: 375px) {
  .navLink {
    padding: 20px 15px;
  }
}
