.paginationContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 25px;
}
.arrowLeft {
  transform: rotate(180deg) !important;
}

.arrowRight,
.arrowLeft {
  user-select: none;
  color: #40489c;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.arrowRight:hover,
.arrowLeft:hover {
  transform: scale(120%);
  transition: all 0.2s ease-in-out;
}

.arrowLeft:hover {
  transform: scale(120%) rotate(180deg);
  transition: all 0.2s ease-in-out;
}

.paginationContainer ul {
  list-style-type: none;
  display: flex;
  padding: 0;
  margin: 0 15px;
}

.currentPage {
  position: relative;
  background-color: #40489c;
  color: #fff !important;
  box-shadow: 0 5px 25px hsla(235, 59%, 75%, 0.75);
}

.dots {
  cursor: auto !important;
}

.pageNumber {
  user-select: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  margin-right: 10px;
  color: #40489c;
  height: 40px;
  font-weight: 600;
  border: 2px solid transparent;
  border-radius: 10px;
}
.pageNumber:not(.dots):hover {
  border: 2px solid #40489c;
  border-radius: 10px;
  transition: all 0.15s ease-in-out;
}

.pageNumber:last-child {
  margin: 0;
}

.hidden {
  opacity: 0;
}
