.tagInputContainer {
  display: flex;
  align-items: center;
  width: 100%;
  height: 45px;
  background-color: white;
  padding: 10px 15px;
  font-size: 1em;
  font-weight: 500;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
  outline: none;
  box-sizing: border-box;
}

.filled {
  padding: 5px;
}

.input {
  height: 40px;
  width: 100%;
  font-size: 1em;
  outline: none;
  border: 0;
  color: #40489c;
  font-size: 1em;
  font-weight: 500;
}

.input::placeholder {
  font-weight: 400;
  color: rgba(0, 0, 0, 0.35);
}

.tag {
  display: flex;
  height: fit-content;
  align-items: center;
  border-radius: 5px;
  user-select: none;
  width: fit-content;
  background-color: #40489c;
  padding: 5px 5px 5px 10px;
  margin-right: 5px;
  color: white !important;
  font-weight: 500;
}

.cross {
  padding: 1px;
  margin-left: 10px;
  border-radius: 100px;
  cursor: pointer;
  transition: all 0.1s ease;
}

.cross:hover {
  background-color: rgba(255, 255, 255, 0.15);
  transition: all 0.1s ease;
}
