.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  box-sizing: border-box;
  width: 100%;
  padding: 0 100px;
  height: 75px;
  font-weight: 600;
  color: #272c5b;
  background-color: white;
  box-shadow: 0 15px 25px hsla(235, 25%, 50%, 0.1);
}

.hamburger {
  position: relative;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 40px;
  height: 25px;
  cursor: pointer;
  overflow: hidden;
}

.hamburgerLine1,
.hamburgerLine2,
.hamburgerLine3 {
  width: 40px;
  height: 3px;
  border-radius: 150px;
  background-color: #2f356f;
  transform-origin: 0 0;
  white-space: nowrap;
  transition: all 0.2s ease-in-out;
}

.hamburgerCheckbox {
  display: none;
}

.active {
  height: 40px;
  transition: all 0.2s ease-in-out;
}

.active :is(.hamburgerLine1, .hamburgerLine2, .hamburgerLine3) {
  position: absolute;
  width: 30px;
  top: 50%;
  left: 50%;
}

.active :is(.hamburgerLine1, .hamburgerLine2) {
  transform: rotate(45deg) translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}
.active .hamburgerLine3 {
  transform: rotate(-45deg) translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}

.headerLogo {
  font-size: 1.5em;
  font-weight: 600;
  color: #2f356f;
  text-decoration: none;
  transition: all 0.1s ease-in-out;
}

.headerLogo span {
  color: #ff9633;
}

.headerLogo:hover {
  color: #6871d8;
  transition: all 0.1s ease-in-out;
}

.headerNav {
  width: clamp(200px, 60%, 950px);
  height: 100%;
  min-width: 400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.headerNav a {
  color: #2f356f;
  text-decoration: none;
  font-size: 1.25rem;
  transition: all 0.1s ease-in-out;
  user-select: none;
}

.headerLink {
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
}

.currentPage::after {
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 4px;
  background-color: #ff9633;
  border-radius: 50px 50px 0 0;
}
.headerLink ::after {
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  opacity: 0;
  height: 0px;
  border-radius: 50px 50px 0 0;
  background-color: hsl(235, 59%, 80%);
  transition: all 0.15s ease-in-out;
}
.headerLink:hover ::after {
  height: 4px;
  opacity: 1;
  transition: all 0.15s ease-in-out;
}

.headerNav a:hover {
  color: #6871d8;
  transition: all 0.1s ease-in-out;
}

.headerProfile {
  width: 50px;
  transition: all 0.15s ease-in-out;
  cursor: pointer;
}
.headerProfile:hover {
  color: #6871d8;
  transition: all 0.15s ease-in-out;
}

.mobileNav {
  position: fixed;
  opacity: 0.5;
  left: -300px;
  display: none;
  flex-direction: column;
  width: 275px;
  height: 100vh;
  z-index: 90;
  font-size: 1.25em;
  font-weight: 600;
  background-color: hsla(235, 25%, 90%, 0.65);
  backdrop-filter: blur(15px);
  box-shadow: 5px 0 25px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
}

.mobileNav a {
  color: #2f356f;
  text-decoration: none;
  padding-left: 26px;
}

.mobileNav .headerLink {
  height: fit-content;
  min-height: 75px;
}

.showMobileNav {
  left: 0;
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

.profile {
  position: relative;
  display: flex;
  align-items: center;
}

.profileInput {
  display: none;
}

.profileNav {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  right: -53px;
  top: 60px;
  border-radius: 0 0 10px 10px;
  padding: 0 25px;
  opacity: 0;
  overflow-y: hidden;
  max-height: 0px;
  box-sizing: border-box;
  white-space: nowrap;
  font-size: 1em;
  background-color: white;
  box-shadow: 0 15px 25px hsla(235, 25%, 50%, 0.1);
  transition: all 0.2s ease-in-out;
}

.dropDownActive {
  padding: 25px 25px 10px 25px;
  max-height: 150px;
  opacity: 1;
  transition: all 0.2s ease-in-out;
}

.profileNav a {
  user-select: none;
  color: #272c5b;
  margin-bottom: 15px;
  text-decoration: none;
  transition: all 0.1s ease-in-out;
}
.profileNav a:hover {
  color: #6871d8;
  transition: all 0.1s ease-in-out;
}

.profileArrow {
  color: #272c5b;
  transform: rotate(180deg);
  cursor: pointer;
}

@media only screen and (max-width: 1000px) {
  .header {
    padding: 0 50px;
    height: 60px;
  }

  .headerLogo {
    font-size: 0.97em;
  }
  .headerNav a {
    font-size: 0.9em;
  }

  .profileNav {
    top: 50px;
  }

  .headerProfile {
    width: 40px;
    height: 40px;
  }
}

@media screen and (max-width: 750px) {
  .header {
    padding: 0 30px;
  }

  .hamburger {
    display: flex;
    margin-right: 25px;
  }

  .mobileNav {
    display: flex;
  }

  .headerNavLeft {
    display: flex;
    align-items: center;
  }
  .headerLogo {
    font-size: 1.25em;
  }

  .headerProfile {
    width: 40px;
    height: 40px;
  }

  .headerNav {
    display: none;
  }
} ;
