.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: clamp(275px, 75%, 900px);
  margin: 0 auto;
  max-width: 600px;
}

.absoluteContainer {
  position: relative;
  height: calc(100vh - 70px);
  width: clamp(275px, 75%, 600px);
}

.firstStep {
  position: absolute;
  width: 100%;
  height: auto;
  margin: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  animation: showStep 0.3s forwards ease-in-out;
}

.errorMessage {
  font-size: 0.9em;
  color: #e15554;
}

.smallStep {
  position: absolute;
  width: clamp(300px, 75%, 750px);
  height: auto;
  margin: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  animation: showStep 0.3s forwards ease-in-out;
}

.step {
  margin: 48px 0;
  width: 100%;
  opacity: 1;
  animation: showStep 0.3s forwards ease-in-out;
}

.hiddenStep {
  display: none;
  position: absolute;
  opacity: 0;
  z-index: -500;
  animation: hideStep 0.2s forwards ease-in-out;
}

@keyframes showStep {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes hideStep {
  from {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  to {
    display: none;
    left: 2000px;
  }
}

.title {
  position: relative;
  width: fit-content;
  margin-bottom: 25px;
}

.title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  border-radius: 100px;
  background-color: #ff9633;
}

.description {
  margin: 0;
  margin-bottom: 15px;
}

.nextArrow {
  margin-left: 10px;
  transition: all 0.3s ease-in-out;
}
.previousArrow {
  margin-right: 10px;
  transform: rotate(180deg);
}

.nextButton:hover .nextArrow {
  margin-left: 20px;
  transition: all 0.3s ease-in-out;
}

.firstStepDescription {
  margin: 0;
  margin-bottom: 50px;
  font-size: 1.1em;
}

.sectionTitle {
  margin: 0;
  margin-bottom: 25px;
  font-size: 1.25em;
}

.label {
  font-weight: 600;
  margin-right: 15px;
  margin-bottom: 7.5px;
}

.formSection {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 15px;
}

.radio {
  display: flex;
  align-items: center;
  margin-right: 15px;
}
.radio input {
  margin-right: 5px;
}

.input {
  color: #40489c;
  padding: 5px 10px;
  font-size: 1em;
  font-weight: 400;
  border: 0;
  border: 1.5px solid rgb(200, 200, 200);
  border-radius: 5px;
  width: 100%;
  outline: none;
  box-sizing: border-box;
  margin: 1px;
  transition: all 0.1s ease-in-out;
}

.input:is(:focus) {
  margin: 0.5px;
  border: 2px solid hsl(235, 42%, 60%);
  transition: all 0.1s ease-in-out;
}

.inputError {
  border: 2px solid #e15554;
}

.input::placeholder {
  font-weight: 400;
  color: rgba(0, 0, 0, 0.3);
}

.buttons {
  margin-top: 25px;
}

.fileLabel {
  display: flex;
  align-items: center;
  width: fit-content;
  font-weight: 500;
  font-size: 1em;
  padding: 7.5px;
  border-radius: 10px;
  border: 2.5px solid #272c5b;
  cursor: pointer;
  margin-bottom: 50px;
  transition: all 0.1s ease-in-out;
}

.fileLabel:hover {
  background-color: #272c5b;
  color: white;
  transition: all 0.1s ease-in-out;
}
.fileInput {
  display: none;
}

.fileLabel span {
  margin-right: 5px;
}

.fileLabel svg {
  margin-right: 10px;
  width: 30px;
  height: auto;
}

.numberSelector {
  display: flex;
  width: 50%;
  min-width: 300px;
  justify-content: space-between;
  border-bottom: solid 1px rgba(0, 0, 0, 0.3);
  margin-bottom: 25px;
}

.numberType {
  position: relative;
  user-select: none;
  cursor: pointer;
  font-weight: 600;
  padding: 7.5px 12.5px;
  border-radius: 5px 5px 0 0;
}

.numberSelected {
  background-color: rgba(104, 113, 216, 0.1);
}

.numberSelected::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  bottom: -2.97px;
  left: 0;
  background-color: #6871d8;
}

.cityInput {
  padding-right: 15px;
}
.postCode {
  min-width: 130px;
}

@media screen and (max-width: 500px) {
  .container,
  .absoluteContainer {
    font-size: 0.9em;
  }
}
