.contentPage {
  width: 550px;
}

.contentPage :is(input, button) {
  color: #272c5b;
}

.pageTitle {
  position: relative;
  margin: 0;
  height: 80px;
  width: fit-content;
  display: flex;
  margin-bottom: 65px;
  align-items: center;
}
.pageTitle:after {
  content: "";
  position: absolute;
  bottom: 15px;
  left: 0px;
  width: 100%;
  height: 5px;
  border-radius: 100px;
  background-color: #ff9633;
}

.sectionTitle {
  font-size: 1.5em;
}

.sectionTitle::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100px;
  height: 5px;
  color: rgba(0, 0, 0, 0.15);
}

.formSection {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 25px 0;
}

.label {
  font-size: 1.1em;
  font-weight: 600;
  margin-bottom: 10px;
}

.input {
  height: 45px;
  position: relative;
}

.input[type="text"],
.input[type="number"] {
  position: relative;
  width: 50%;
  color: #40489c;
  padding: 0 15px;
  font-size: 1em;
  font-weight: 500;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
  outline: none;
  box-sizing: border-box;
}

.priceContainer {
  position: relative;
  width: 100%;
  height: 45px;
}

.euro {
  position: absolute;
  user-select: none;
  right: calc(50% + 10px);
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
}

.info {
  margin: 10px 0 15px 0;
}

.textareaContainer {
  position: relative;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
  padding: 15px 15px 5px 15px;
}

.textarea {
  width: 100%;
  max-width: 100%;
  min-width: 100%;
  min-height: 75px;
  font-size: 1em;
  color: #40489c;
  font-weight: 500;
  border: 0;
  border-radius: 10px;
  outline: none;
  box-sizing: border-box;
}

.charCount {
  font-size: 0.75em;
  margin-left: -5px;
  box-sizing: content-box;
  user-select: none;
}

.select {
  height: 45px;
  padding: 0 15px;
  width: 100%;
  font-size: 1em;
  color: #40489c;
  font-weight: 500;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
  outline: none;
  cursor: pointer;
  appearance: none;
  -moz-appearance: none; /* Firefox */
  -webkit-appearance: none; /* Safari and Chrome */
  background: url("./arrow-down.svg");
  background-position: right;
  background-repeat: no-repeat;
  background-position-x: 265px;
}

.input[type="text"]::placeholder,
.input[type="number"]::placeholder,
.ageInput::placeholder,
.textarea::placeholder {
  font-weight: 400;
  color: rgba(0, 0, 0, 0.35);
}

.genderSelector {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 25px 0;
  padding: 15px;
  box-sizing: border-box;
  width: 100%;
  height: 230px;
  background-color: rgba(64, 72, 156, 0.075);
  border-radius: 15px;
}

.genderSelection {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 150px;
  cursor: pointer;
}
.gender {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.genderText {
  display: flex;
  align-items: center;
  cursor: pointer;
  height: 50px;
  margin-bottom: 25px;
  font-size: 1.25em;
  font-weight: 600;
  user-select: none;
}

.genderSelected {
  position: absolute;
  top: 15px;
  z-index: -1;
  width: 200px;
  height: 200px;
  background-color: rgba(64, 72, 156, 0.1);
  border-radius: 10px;
}

@keyframes move-left {
  from {
    left: 15px;
  }
  to {
    left: 100%;
    transform: translateX(calc(-100% - 15px));
  }
}
@keyframes move-right {
  from {
    left: 100%;
    transform: translateX(calc(-100% - 15px));
  }
  to {
    left: 15px;
  }
}

.femaleSelected {
  animation: move-left 0.7s forwards;
}

.maleSelected {
  animation: move-right 0.7s forwards;
}

.genderSelection svg {
  width: 50%;
  height: 50%;
  transition: all 0.25s ease-in-out;
}

.genderSelection:hover svg {
  width: 55%;
  height: 55%;
  transition: all 0.25s ease-in-out;
}

.input[type="radio"] {
  display: none;
}

.dateInputContainer {
  position: relative;
  display: flex;
  width: 50%;
  min-width: 300px;
  align-items: center;
  color: #40489c !important;
  height: 45px;
  padding: 0 15px;
  font-size: 1em;
  font-weight: 500;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
  outline: none;
  box-sizing: border-box;
}

.dateInput {
  border: 0;
  color: #40489c !important;
  width: 100%;
  outline: none;
  font-size: 1em;
  font-weight: 500;
}

.dateInput::-webkit-datetime-edit-text {
  margin: 0 10px;
}
.dateInput::-webkit-calendar-picker-indicator {
  background: url("calendar.svg");
  cursor: pointer;
}

.input[type="file"] {
  display: none;
}

.imageInputRow {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.inputImg {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 175px;
  height: 175px;
  background-color: rgba(64, 72, 156, 0.075);
  border-radius: 15px;
  cursor: pointer;
  overflow: hidden;
}

.plus {
  width: 33%;
  height: auto;
  transition: all 0.3s ease-in-out;
}

.inputImg:hover .plus {
  width: 43%;
  transition: all 0.3s ease-in-out;
}

.imagePreview {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.1s ease-in-out;
}

.inputImg:hover .imagePreview {
  filter: blur(5px) brightness(85%);
  transition: all 0.1s ease-in-out;
}

.modifyText {
  position: absolute;
  z-index: 1;
  opacity: 0;
  color: white;
  font-weight: 500;
  user-select: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.1s ease-in-out;
}

.modifyText:hover {
  text-decoration: underline;
}

.inputImg:hover .modifyText {
  opacity: 1;
  transition: all 0.1s ease-in-out;
}

.filenames {
  margin: 10px 0;
  display: flex;
  max-width: 175px;
  justify-content: center;
}

.buttons {
  margin: 50px auto;
}

.healthContainer {
  display: flex;
  justify-content: space-between;
  width: 115%;
}

.column {
  list-style-type: none;
  padding: 0;
}
.healthContainer .column:first-child {
  margin-right: 25px;
}

.column li {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.column li .label {
  margin: 0;
  font-weight: 500;
}

.test {
  display: flex;
  justify-content: space-between;
}

.column .select {
  width: 100%;
  background: url("./arrow-down.svg");
  background-position: right;
  background-repeat: no-repeat;
  background-position-x: 80px;
}

.testLabel {
  margin-right: 15px !important;
}
.column li input {
  width: 15px;
  height: 15px;
  cursor: pointer;
  margin-right: 15px;
}
.column li:last-child {
  margin-bottom: 0px;
}

@media screen and (max-width: 875px) {
  .contentPage {
    width: clamp(300px, calc(100% - 25px), 875px);
  }

  .healthContainer {
    width: 100%;
  }
}
@media screen and (max-width: 700px) {
  .healthContainer {
    flex-direction: column;
  }

  .test {
    display: flex;
    justify-content: flex-start;
  }
  .testSelect {
    width: 125px;
  }
}

@media screen and (max-width: 575px) {
  .genderSelector {
    margin: 25px 0;
    padding: 5px;
    box-sizing: border-box;
    width: 100%;
    height: 155px;
    background-color: rgba(64, 72, 156, 0.075);
    border-radius: 15px;
  }

  .genderSelection {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 125px;
    height: 100px;
    cursor: pointer;
  }
  .genderText {
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 50px;
    margin-bottom: 25px;
    font-size: 1.25em;
    font-weight: 600;
    user-select: none;
  }

  .genderSelected {
    position: absolute;
    top: 15px;
    z-index: -1;
    width: 100px;
    height: 125px;
    background-color: rgba(64, 72, 156, 0.1);
    border-radius: 10px;
  }

  .healthContainer {
    flex-direction: column;
  }

  .pageTitle {
    margin-bottom: 25px;
  }

  .priceContainer {
    min-width: 300px;
  }

  .euro {
    left: 260px;
  }

  .input {
    min-width: 300px;
  }

  .contentPage {
    width: 90%;
    margin: auto;
  }

  .inputImg {
    width: 90px;
    height: 90px;
  }
}
