.content {
  width: clamp(300px, calc(100% - 25px), 950px);
}

.pageTitle {
  position: relative;
  margin: 0;
  height: 80px;
  width: fit-content;
  display: flex;
  margin-bottom: 65px;
  align-items: center;
}
.pageTitle:after {
  content: "";
  position: absolute;
  bottom: 15px;
  left: 0px;
  width: 100%;
  height: 5px;
  border-radius: 100px;
  background-color: #ff9633;
}

.helpHeader {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-weight: 500;
  background-color: white;
  width: 100%;
  box-sizing: border-box;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 5px 25px hsla(235, 59%, 75%, 0.15);
}

.title {
  margin: 0;
  margin-bottom: 25px;
}

.contact {
  display: flex;
  align-items: center;
}

.contact:nth-child(1) {
  margin-bottom: 15px;
}

.contactList {
  padding: 0;
  margin: 0;
}

.contact svg {
  margin-right: 15px;
}

@media screen and (max-width: 575px) {
  .pageTitle {
    position: relative;
    margin: 0;
    height: 80px;
    width: fit-content;
    display: flex;
    margin-bottom: 25px;
    align-items: center;
  }
  .content {
    margin: 0 auto;
  }
}
