.panel {
  display: flex;
}
.content {
  padding-top: 50px;
  padding-left: 50px;
  box-sizing: border-box;
  height: calc(100vh - 75px);
  width: 100%;
  overflow-y: scroll;
}

@media screen and (max-width: 750px) {
  .content {
    padding-left: 25px;
  }
}

@media screen and (max-width: 575px) {
  .panel {
    flex-direction: column-reverse;
  }
  .content {
    padding: 0;
    margin: auto;
    width: 100%;
    margin-bottom: 75px;
    font-size: 0.8em !important;
  }
}
