.accountContainer {
  display: flex;
  flex-direction: column;
  width: 35%;
  width: clamp(300px, 75%, 675px);
  margin: 0 auto;
  padding: 50px 0;
}

.accountCard {
  background-color: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
}

.accountBody {
  margin: 25px;
}

.accountHeader {
  width: 100%;
  height: fit-content;
  display: flex;
  background-color: white;
  padding: 25px;
  box-sizing: border-box;
  border-radius: 15px 15px 0 0;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.25);
}

.accountHeaderText {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.title {
  margin-bottom: 50px;
}

.profileImg {
  border-radius: 100%;
  width: 25%;
  max-width: 200px;
  margin-right: 50px;
}

.name {
  font-size: 1.75em;
  line-height: 1.25em;
  margin: 0;
}

.departement {
  display: flex;
  align-items: center;
  margin: 0;
  font-size: 1.25em;
  color: #40489c;
}

.departementNumber {
  padding-left: 10px;
  font-weight: 600;
  font-size: 0.75em;
}

.accountSection {
  margin-bottom: 50px;
}

.identity {
  font-size: 1rem;
}
.info {
  display: flex;
  align-items: baseline;
}

.label {
  font-weight: 600;
  margin-right: 15px;
  margin-bottom: 7.5px;
}

.data {
  margin: 0;
}

.modifyInfo {
  display: flex;
  flex-direction: column;
  margin: 25px 0 50px 0;
  width: 75%;
}

.cityInput {
  margin-right: 15px;
  margin-bottom: 0 !important;
  width: 75% !important;
}
.cityInput:is(:valid, :focus) {
  margin-right: 15px;
}

.input {
  color: #40489c;
  padding: 10px;
  font-size: 1em;
  font-weight: 500;
  border: 0;
  border: 1.5px solid rgb(200, 200, 200);
  border-radius: 5px;
  width: 100%;
  max-width: 500px;
  outline: none;
  box-sizing: border-box;
  margin: 1px;
  margin-bottom: 10px;
  transition: all 0.1s ease-in-out;
}

.input:focus {
  margin: 0;
  margin-bottom: 9px;
  border: 2.5px solid hsl(235, 42%, 60%);
  transition: all 0.1s ease-in-out;
}

.input::placeholder {
  font-weight: 400;
  color: rgba(0, 0, 0, 0.3);
}

.accountActions {
  display: flex;
  flex-direction: column;
}

.accountActions .deleteAccount {
  margin-top: 50px;
}

.button {
  margin-top: 10px;
}

@media screen and (max-width: 525px) {
  .title {
    margin: 5px 0 25px 0;
  }

  .info {
    flex-direction: column;
    margin-bottom: 14px;
  }
  .accountContainer {
    padding-top: 25px;
    font-size: 0.75em;
  }
  .accountSection p {
    font-size: 0.9em;
  }
  .label {
    margin: 0;
  }
  .row {
    flex-direction: column;
    align-items: flex-start;
  }

  .row input {
    width: 100% !important;
  }

  .row button {
    margin-top: 15px;
  }

  .modifyInfo {
    width: 100%;
    height: auto;
  }
  .input {
    height: 35px;
  }
} ;
