.footerContainer {
  position: relative;
  display: flex !important;
  flex-direction: column !important;
  justify-content: flex-end;
  z-index: 100;
}

.logo {
  font-size: 1.5em;
  font-weight: 600;
  color: white;
  text-decoration: none;
  transition: all 0.1s ease-in-out;
}

.logo span {
  color: #ff9633;
}

.footer {
  background-color: #2f356f;
  color: white;
  box-sizing: border-box;
  padding: 5px 20% 25px 20%;
  width: 100%;
  z-index: 100;
  display: flex;
  justify-content: space-between;
}

.footer ul {
  padding: 0;
  list-style-type: none;
  margin: 15px 0 0 0;
}

.footer ul li {
  margin-bottom: 10px;
  font-size: 1em;
  font-weight: 500;
}

.footer a {
  color: white;
  text-decoration: none;
}

.footer ul li:last-child {
  bottom: 0;
}

.shape {
  display: block;
  position: relative;
  background-color: transparent;
  margin: 0;
  width: 100%;
  height: auto;
  fill: #2f356f;
  filter: drop-shadow(0 0px 25px hsla(235, 59%, 50%, 0.35));
}

.credits {
  display: flex;
  background-color: #272c5b;
  color: white;
  font-weight: 300;
  font-size: 0.9em;
  padding: 5px 20% 0 20%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 10px 25px inset rgba(0, 0, 0, 0.2);
}
.credits a {
  white-space: nowrap;
  position: relative;
  color: white;
  width: fit-content;
  text-decoration: none;
}
.credits a:after {
  content: "";
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: white;
  height: 1px;
  text-decoration: none;
}

.socials {
  display: flex;
  margin-top: 15px;
  align-items: center;
}

.socials svg {
  width: 25px;
  height: 25px;
  margin-right: 10px;
}

@media screen and (max-width: 800px) {
  .footer {
    padding: 25px 15%;
  }
  .credits {
    height: auto;
    flex-direction: column;
    padding: 15px 0;
  }
}
@media screen and (max-width: 650px) {
  .logo {
    font-size: 1.5em;
  }
  .footer {
    font-size: 0.8em;
  }
}

@media screen and (max-width: 425px) {
  .logo {
    width: min-content;
  }

  .credits {
    font-size: 0.75em;
  }

  .footer {
    padding: 25px 7.5%;
  }
}
@media screen and (max-width: 375px) {
  .footer {
    font-size: 0.75em;
  }
}
