.pageTitle {
  position: relative;
  margin: 0;
  height: 80px;
  width: fit-content;
  display: flex;
  margin-bottom: 65px;
  align-items: center;
}
.pageTitle:after {
  content: "";
  position: absolute;
  bottom: 15px;
  left: 0px;
  width: 100%;
  height: 5px;
  border-radius: 100px;
  background-color: #ff9633;
}

.settingsContent {
  width: clamp(300px, calc(100% - 25px), 950px);
}

.settingsHeader {
  background-color: white;
  width: clamp(300px, calc(50% - 12.5px), 700px);
  margin-bottom: 25px;
  box-sizing: border-box;
  padding: 15px 25px;
  border-radius: 10px;
  box-shadow: 0 5px 25px hsla(235, 59%, 75%, 0.15);
}

.settingsHeader h2 {
  font-size: 1.25rem;
  margin: 0;
}

.infoContainer {
  width: clamp(300px, 100%, 800px);
  height: fit-content;
  box-sizing: border-box;
  padding: 25px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 5px 25px hsla(235, 59%, 75%, 0.2);
  margin: 0 25px 25px 0;
}
.infoContainer h2 {
  margin: 0 0 25px 0;
}
.infoText {
  margin: 0 0 25px 0;
}

.formSection {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 15px;
}

.separator {
  margin-right: 25px;
}

.submit {
  margin-top: 25px;
}

.gender {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.radio {
  margin-right: 15px;
}
.radio input {
  margin-right: 5px;
}

.settingsFooter {
  background-color: white;
  width: clamp(300px, calc(50% - 12.5px), 700px);
  margin-bottom: 25px;
  box-sizing: border-box;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 5px 25px hsla(235, 59%, 75%, 0.15);
}

.settingsFooter h2 {
  margin: 0 0 25px 0;
}

.footerRow {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

@media screen and (max-width: 1249px) {
  .settingsFooter .separator {
    margin-bottom: 25px;
  }

  .city {
    width: 50% !important;
  }

  .postCode {
    width: 50% !important;
  }
}

@media screen and (max-width: 1050px) {
  .row {
    flex-direction: column;
  }
  .city {
    width: 50% !important;
  }

  .postCode {
    width: 50% !important;
  }
}
@media screen and (max-width: 575px) {
  .settingsContent {
    margin: auto;
  }

  .pageTitle {
    margin-bottom: 25px;
  }

  .formSection input,
  .radio {
    display: flex;
    align-items: center;
    height: 35px;
  }

  .row {
    flex-direction: column;
  }

  .infoContainer {
    padding: 25px;
  }

  .settingsFooter {
    margin-bottom: 75px;
  }
}
