.active {
  animation: show 0.75s ease-in-out forwards;
}

.vanish {
  opacity: 1;
  transform: translateY(-525px);
  animation: hide 0.75s ease-in-out backwards;
}

@keyframes show {
  0% {
    opacity: 0;
    transform: translateY(0);
  }

  90% {
    opacity: 1;
    transform: translateY(-535px);
  }

  100% {
    opacity: 1;
    transform: translateY(-525px);
  }
}

@keyframes hide {
  0% {
    opacity: 1;
    transform: translateY(-525px);
  }

  10% {
    opacity: 1;
    transform: translateY(-535px);
  }

  100% {
    opacity: 0;
    transform: translateY(0);
  }
}

.success {
  background-color: hsl(168, 95%, 26%);
  box-shadow: 0 15px 25px hsla(168, 95%, 26%, 0.3);
}

.success .progressBarContainer {
  background-color: hsl(168, 95%, 21%);
}

.success .progressBar {
  background-color: hsl(167, 40%, 45%) !important;
}
.success :is(.text, svg) {
  color: hsl(150, 30%, 85%);
}

.error {
  background-color: #b74645;
  box-shadow: 0 15px 25px rgba(181, 71, 69, 0.25);
}

.error .progressBarContainer {
  background-color: #7f3130;
}

.error .progressBar {
  background-color: hsl(0, 60%, 65%) !important;
}
.error :is(.text, svg) {
  color: hsl(0, 75%, 85%);
}

.info,
.loading {
  background-color: #4c54a3;
  box-shadow: 0 15px 25px rgba(154, 160, 229, 0.75);
}

.info .progressBarContainer,
.loading .progressBarContainer {
  background-color: #383f89;
}

.info .progressBar,
.loading .progressBar {
  background-color: hsl(235, 30%, 60%) !important;
}
.info :is(.text, svg),
.loading :is(.text, svg) {
  color: hsl(236, 32%, 75%);
}

.notification {
  position: fixed;
  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  opacity: 0;
  bottom: -500px;
  right: 25px;
  min-width: 300px;
  width: fit-content;
  max-width: 400px;
  border-radius: 7px;
  color: white;
  overflow: hidden;
  user-select: none;
}

.notification * {
  margin: 0;
}

.notificationContent {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  margin: 0;
}
.icon {
  display: flex;
  margin-right: 10px;
}

.text {
  font-size: 1em;
  font-weight: 600;
  margin-right: 5px;
}

.progressBarContainer {
  position: relative;
  width: 100%;
  height: 7px;
}

.active .progressBar {
  position: absolute;
  z-index: 150;
  top: 0;
  left: 0;
  width: 110%;
  height: 10px;
  border-radius: 0 3px 3px 0;
  animation: countdown 5s forwards linear !important;
}

.loading .progressBar {
  width: 15%;
  border-radius: 3px;
  animation: loading-progressbar 4s infinite ease-in-out !important;
}

@keyframes loading-progressbar {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(565%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes countdown {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-110%);
  }
}

@media screen and (max-width: 575px) {
  .notification {
    bottom: -450px !important;
    right: 9px !important;
  }
} ;
