.register,
.login,
.forgotPassword,
.resetPassword {
  display: flex;
  flex-direction: column;
  width: 25%;
  min-width: 300px;
  height: calc(100vh - 75px);
  justify-content: center;
  margin: 0 auto;
}

.title {
  white-space: nowrap;
}

.formSection {
  display: flex;
  flex-direction: column;
  margin: 10px 0;
}

.formSection label {
  font-weight: 600;
  margin-bottom: 10px;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.bottomRow {
  margin-top: 25px;
}

@media screen and (max-width: 1300px) {
  .bottomRow {
    flex-direction: column;
  }
  .connectText {
    margin-top: 15px;
  }
}

.half {
  width: 49%;
}

.connectText {
  text-align: center;
  font-weight: 500;
}

.connectText a {
  color: #6871d8;
}

.rememberMe {
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.rememberMe label {
  margin: 0 0 0 15px;
}
.rememberMe input {
  width: 15px;
  height: 15px;
}

.formSection a {
  color: #6871d8;
  margin-top: 5px;
  font-size: 0.9em;
}

.forgotSection {
  margin-bottom: 26px;
}
