.container {
  width: 100%;
  padding: 0 50px;
  box-sizing: border-box;
  height: calc(100vh - 75px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.container h1 {
  font-size: 9em;
  margin: 0;
}
.container span {
  font-size: 1.25em;
}

.container a {
  margin-top: 50px;
}

@media screen and (max-width: 768px) {
  .container span {
    font-size: 1em;
  }
  .container h1 {
    font-size: 5em;
  }
}
