.content {
  width: clamp(300px, calc(100% - 25px), 950px);
}

.pageTitle {
  position: relative;
  margin: 0;
  height: 80px;
  width: fit-content;
  display: flex;
  margin-bottom: 65px;
  align-items: center;
}
.pageTitle:after {
  content: "";
  position: absolute;
  bottom: 15px;
  left: 0px;
  width: 100%;
  height: 5px;
  border-radius: 100px;
  background-color: #ff9633;
}

.profileHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.1rem;
  font-weight: 500;
  background-color: white;
  width: clamp(300px, 100%, 700px);
  height: 75px;
  box-sizing: border-box;
  padding: 15px 15px 15px 25px;
  border-radius: 10px;
  box-shadow: 0 5px 25px hsla(235, 59%, 75%, 0.15);
}
.profileBody {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 1.1rem;
  margin: 25px 0;
  font-weight: 500;
  background-color: white;
  width: clamp(300px, 100%, 700px);
  height: 100%;
  box-sizing: border-box;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 5px 25px hsla(235, 59%, 75%, 0.15);
}

.modifyTitle {
  margin: 0;
  margin-bottom: 25px;
}

.infoText {
  margin-top: 10px;
  font-weight: 400;
  font-size: 1rem;
  width: 90%;
}

.sectionTitle {
  font-size: 1.25em;
}

.sectionTitle::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100px;
  height: 5px;
  color: rgba(0, 0, 0, 0.15);
}

.formSection {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 25px 0;
}

.halfSection {
  display: flex;
  flex-direction: column;
  margin-top: 0;
  width: 49%;
}

.formSection input {
  width: clamp(200px, 75%, 400px);
}

.textarea {
  padding: 10px;
  max-width: 100%;
  min-height: 125px;
}

.textarea:focus {
  padding: 9px;
  max-width: 100%;
  min-height: 125px;
}

.social {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 5px 0;
}
.social svg {
  width: 30px;
  height: 30px;
  margin-right: 15px;
}

.fileLabel {
  display: flex;
  align-items: center;
  width: fit-content;
  font-weight: 500;
  font-size: 1em;
  padding: 7.5px;
  border-radius: 10px;
  border: 2.5px solid #272c5b;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}

.fileLabel:hover {
  background-color: #272c5b;
  color: white;
  transition: all 0.1s ease-in-out;
}
.fileInput {
  display: none;
}

.fileLabel span {
  margin-right: 5px;
}

.fileLabel svg {
  margin-right: 10px;
  width: 30px;
  height: auto;
}

@media screen and (max-width: 1150px) {
  .row {
    flex-direction: column;
  }
}
@media screen and (max-width: 575px) {
  .content {
    margin: 0 auto;
  }

  .pageTitle {
    margin-bottom: 25px;
  }

  .profileHeader {
    font-size: 1.1em;
    height: auto;
    flex-direction: column;
  }

  .profileBody {
    font-size: 1.1em;
    width: 100%;
  }
  .infoText {
    font-size: 0.9em;
  }

  .formSection input {
    height: 35px;
  }

  .formSection input,
  .halfSection {
    min-width: 100% !important;
  }

  .headerText {
    margin-bottom: 15px;
  }
  .social {
    width: 100%;
    margin-bottom: 10px;
  }

  .social svg {
    width: 25px !important;
    height: 25px !important;
  }
  .social input {
    flex: 1;
    margin: 0;
    min-width: 0 !important;
  }
}
