.searchBar {
  width: clamp(285px, 60%, 1000px);
  max-width: 800px;
  border-radius: 10px;
  margin: 50px auto;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
  overflow: hidden;
}

.search {
  display: flex;
  margin: auto;
  color: #40489c;
  background-color: #fff;
}

.searchInput {
  font-family: "Poppins", "Helvetica Neue", sans-serif;
  flex: 1;
  min-width: 110px;
  height: auto;
  outline: none;
  border-radius: 10px;
  padding: 0 0 0 10px;
  font-size: 1rem;
  border: none;
}

.searchIconContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  color: rgb(190, 190, 190);
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}
.searchIcon {
  width: 25px;
  height: 25px;
}

.searchIconContainer:hover {
  color: #6871d8;
  transition: all 0.15s ease-in-out;
}
