.content {
  position: relative;
  width: clamp(300px, calc(100% - 25px), 700px);
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 50px auto 125px auto;
}

.imageContainer {
  border-radius: 10px;
  position: relative;
  width: clamp(250px, 100%, 925px);
  height: auto;
  background-color: white;
  object-fit: cover;
}

.imageContainer::before {
  content: "";
  display: block;
  width: 100%;
  padding-top: 56.25%;
}

.image {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.assoHeader {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  margin-bottom: 25px;
  border-radius: 10px;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
}

.assoHead {
  background-color: white;
  padding: 20px 25px;
}

.assoBody {
  width: 100%;
}

.name {
  font-size: 1.75em;
  margin: 0;
  width: fit-content;
}

.section {
  margin: 25px 0;
}

.title {
  position: relative;
  width: fit-content;
  padding-bottom: 5px;
  margin: 15px 0;
}

.description {
  margin: 5px 0;
  text-align: justify;
  white-space: pre-wrap;
}

.contactList {
  padding: 0;
}

.contact {
  display: flex;
  font-size: 1.1em;
  font-weight: 500;
  align-items: center;
  margin: 15px 0;
}

.contact svg {
  width: 30px;
  height: 30px;
  margin-right: 15px;
}

.socials {
  margin-top: 25px;
  display: flex;
  width: 100%;

  box-sizing: border-box;
  height: 75px;
  justify-content: space-evenly;
  align-items: center;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 15px 25px hsla(235, 59%, 75%, 0.2);
}
.social {
  display: flex;
  align-items: center;
  font-size: 1.1em;
  font-weight: 600;
  text-decoration: none;
  color: #272c5b;
  transition: all 0.2s ease-in-out;
}

.social:hover {
  color: #6871d8;
  transition: all 0.2s ease-in-out;
}

.social svg {
  width: 30px;
  height: 30px;
  margin-right: 15px;
}

@media screen and (max-width: 750px) {
  .content {
    font-size: 0.9em;
  }

  .section {
    margin-top: 0;
  }

  .assoHead {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 0.9em;
    height: 75px;
    padding: 0 0 0 15px;
  }
  .assoHeader {
    margin-bottom: 15px;
  }

  .contactList {
    font-size: 0.9em;
  }
  .contactList svg {
    width: 25px;
    height: 25px;
  }

  .socials {
    flex-direction: column;
    align-items: flex-start;
    width: fit-content;
    padding: 10px 25px;
    height: auto;
  }
  .socials a {
    margin: 10px 0;
  }
}
