.adoptMe {
  width: 90%;
  margin: 0 auto;
  padding-top: 0.1px;
}

.cardGrid {
  width: clamp(300px, 75%, 2000px);
  margin: auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.noResults {
  width: fit-content;
  margin: 0;
}

@media only screen and (max-width: 500px) {
  .noResults {
    font-size: 1.25em;
  }
}
