.popup {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: white !important;
  width: clamp(200px, 75%, 750px);
  height: 300px;
  z-index: 200;
  top: 50% !important;
  left: 50% !important;
  border-radius: 15px;
  white-space: pre-wrap;
  padding: 50px;
}

.activePopup {
  animation: showPopUp 0.5s ease-in-out forwards !important;
}

.vanishPopup {
  animation: hidePopUp 0.5s ease-in-out backwards !important;
}

@keyframes showPopUp {
  0% {
    opacity: 0;
    transform: translate(-50%, -150%);
  }

  100% {
    box-shadow: 0 0 10000px 10000px rgba(0, 0, 0, 0.25);
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

@keyframes hidePopUp {
  0% {
    opacity: 1;
    transform: translate(-50%, -50%);
  }

  100% {
    opacity: 0;
    transform: translate(-50%, -150%);
  }
}

.popup h1 {
  width: 75%;
  margin: 0 0 35px 0;
}

.close {
  position: absolute;
  right: 35px;
  top: 35px;
  width: 35px;
  height: 35px;
  cursor: pointer;
}

.btn {
  width: fit-content;
  align-self: flex-end;
}

@media screen and (max-width: 465px) {
  .popup {
    height: auto;
    font-size: 0.9em;
    padding: 25px;
  }

  .popup h1 {
    font-size: 1.75em;
  }

  .popup input {
    font-size: 0.9em;
  }
  .btn {
    align-self: center;
    margin-top: 25px;
  }
}
