.content {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.shape {
  position: absolute;
  width: 80%;
  min-width: 750px;
  z-index: -1;
  right: 0;
  top: 0;
}

.catShape {
  color: #6871d8;
}
.dogShape {
  color: #ffd232;
}
.rabbitShape {
  color: #e15554;
}
.birdShape {
  color: #03cea4;
}
.rodentShape {
  color: #78c3fb;
}

.listing {
  position: relative;
  margin: 50px auto 125px auto;
  width: clamp(300px, calc(100% - 50px), 950px);
}

.images {
  display: flex;
  justify-content: space-between;
  height: fit-content;
}

.images img {
  border-radius: 10px;
  user-select: none;
}

.imageColumn {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 15%;
  max-height: 100%;
}

.columnImage {
  position: relative;
  margin-right: 25px;
  width: clamp(100px, 100%, 200px);
  max-height: 100%;
}

.columnImage::before {
  content: "";
  display: block;
  width: 100%;
  padding-bottom: 100%;
}

.columnImage img {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}

.imageShown {
  border-radius: 10px;
  position: relative;
  width: clamp(250px, 82.5%, 925px);
  height: auto;
  background-color: white;
}

.imageShown::before {
  content: "";
  display: block;
  width: 100%;
  padding-bottom: 60%;
}

.imageShown img {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.listingHeader * {
  margin: 0;
}

.listingHeader {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 25px;
}

.price {
  font-weight: 600;
  color: #515ac8;
  font-size: 2rem;
}

.name {
  font-size: 2em;
  font-weight: 700;
}

.identity {
  font-size: 1.4rem;
  font-weight: 600;
  line-height: auto;
  margin: 0;
  margin-top: 5px;
}

.genderMale {
  color: #00a6ed;
}
.genderFemale {
  color: #d34f73;
}

.age {
  color: #515ac8;
}

.city {
  font-size: 1.25em;
  color: #40489c;
  margin: 0;
  font-weight: 500;
}

.title {
  margin: 15px 0;
}
.birthDate h3 {
  margin: 15px 0;
  font-weight: 600;
}

.description {
  text-align: justify;
  white-space: pre-wrap;
}

.about,
.health {
  margin: 50px 0;
}

.about ul,
.health ul {
  padding-left: 10px;
}

.about li,
.health li {
  font-size: 1.1rem;
  list-style-type: none;
  font-weight: 500;
}

.health .listingTitle {
  margin-bottom: 0;
}

.about li {
  color: #6871d8;
}

.health li .answer {
  margin-left: 10px;
  color: #6871d8;
  font-weight: 600;
}
.about li:before,
.health li:before {
  content: "-";
  font-size: 1.1em;
  margin-right: 15px;
  color: #6871d8;
}

.good {
  color: #44bba4 !important;
}
.bad {
  color: #e15554 !important;
}

.healthContainer {
  width: clamp(300px, calc(100% - 50px), 950px);
  display: flex;
  justify-content: space-between;
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  bottom: 0;
  width: clamp(300px, calc(100% - 50px), 950px);
  height: 80px;
  background-color: #fff;
  border-radius: 10px 10px 0 0;
  box-shadow: 0 -5px 25px hsla(235, 59%, 75%, 0.2);
  padding: 25px;
  box-sizing: border-box;
}

.menuInfo {
  display: flex;
  flex-direction: column;
}

.date {
  margin-left: 5px;
}

.asso {
  font-size: 1.25rem;
  font-weight: 600;
  text-decoration: none;
  color: #6871d8;
  transition: all 0.1s ease-in-out;
}
.asso:hover {
  color: #515ac8;
  transition: all 0.1s ease-in-out;
}

.menuButton {
  display: flex;
  align-items: center;
  padding: 10px 12.5px;
  font-size: 1.25em;
  font-weight: 500;
  border-radius: 10px;
  text-decoration: none;
  width: fit-content;
  cursor: pointer;
  color: white !important;
  transition: all 0.1s ease-in-out;
  font-weight: 600;
  color: white;
  background-color: #ff9633;
  box-sizing: content-box;
}
.menuButton:hover {
  background-color: #ff891a;
}

.visible {
  opacity: 1;
  visibility: visible;
  transition: all 0.1s ease-in-out;
}

.hidden {
  opacity: 0;
  width: 100%;
  visibility: hidden;
  transition: all 0.1s ease-in-out;
}

@media screen and (max-width: 750px) {
  .healthContainer {
    flex-direction: column;
  }

  .images {
    flex-direction: column-reverse;
  }

  .imageShown {
    width: 100%;
  }

  .columnImage {
    border-radius: 10px;
    position: relative;
    width: clamp(50px, 100%, 100px);
    height: auto;
    background-color: white;
  }

  .columnImage::before {
    content: "";
    display: block;
    width: 100%;
    padding-bottom: 100%;
  }

  .columnImage img {
    margin-right: 15px;
  }

  .imageColumn {
    margin-top: 15px;
    flex-direction: row;
    width: clamp(250px, 75%, 400px);
  }

  /*
  .imageColumn {
    margin-top: 15px;
    width: 100%;
    height: 100px;
    flex-direction: row;
  }
  .columnImage {
    margin-right: 15px;
    width: 100%;
    max-height: 100%;
  }*/
}
@media screen and (max-width: 450px) {
  .content {
    font-size: 0.9em;
  }

  .identity {
    font-size: 1.1rem !important;
  }

  .price {
    font-size: 1.5rem !important;
  }

  .menu {
    height: auto;
    padding: 10px 15px;
  }
  .content li {
    font-size: 1em;
  }
}
@media screen and (max-width: 450px) {
  .menu {
    font-size: 0.9em;
  }
  .menu .asso {
    font-size: 1.25em;
  }
}
